// src/app/app/page.tsx
"use client";

import { useSession } from "next-auth/react";
import Link from "next/link";
import {
  ListBulletIcon,
  PencilIcon,
  BookOpenIcon,
  ClockIcon,
  ArrowUpCircleIcon,
  SparklesIcon, // For AI
  PhotoIcon, // For Images
  CheckCircleIcon, // For Unlimited
  DevicePhoneMobileIcon, // For Devices
} from "@heroicons/react/24/outline";
import { useTier } from "@/lib/db/useTier";

// This is the component for Free users to upsell them
function UpgradeSection() {
  const proFeatures = [
    {
      icon: CheckCircleIcon,
      name: "Go Unlimited",
      description: "Remove all limits on tasks, notes, and Pomodoro history.",
    },
    {
      icon: SparklesIcon,
      name: "AI Weekly Insights",
      description:
        "Get AI-powered summaries of your productivity and diary entries.",
    },
    {
      icon: PhotoIcon,
      name: "Image Uploads",
      description:
        "Visually organize your notes by attaching images and files.",
    },
    {
      icon: DevicePhoneMobileIcon,
      name: "Sync More Devices",
      description:
        "Keep your workflow in sync across all your devices, not just one.",
    },
  ];

  return (
    <div className="space-y-8">
      {/* Current Tier Card */}
      <div className="rounded-lg border border-gray-200 bg-white p-6 shadow-sm">
        <h3 className="text-xl font-semibold text-gray-900">
          You are on the Free Tier
        </h3>
        <p className="mt-1 text-gray-600">
          You have access to all core features with some limitations.
        </p>
      </div>

      {/* Why Go Pro? Section */}
      <div>
        <h2 className="text-2xl font-semibold text-gray-900 mb-5">
          Unlock Your Full Potential with Pro
        </h2>
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
          {proFeatures.map((feature) => (
            <div
              key={feature.name}
              className="group relative flex gap-x-6 rounded-lg p-6 bg-white border border-gray-200 shadow-sm hover:shadow-lg hover:border-blue-500 transition-all duration-300"
            >
              <div className="flex-shrink-0 p-3 bg-blue-50 text-blue-600 rounded-lg group-hover:bg-blue-100 transition-colors">
                <feature.icon className="h-6 w-6" aria-hidden="true" />
              </div>
              <div>
                <h3 className="text-lg font-semibold text-gray-900">
                  {feature.name}
                </h3>
                <p className="mt-1 text-sm text-gray-600">
                  {feature.description}
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Upgrade Button */}
      <div className="bg-gradient-to-r from-blue-600 to-indigo-700 rounded-lg shadow-lg p-8 text-center">
        <h3 className="text-2xl font-bold text-white">Ready to Go Pro?</h3>
        <p className="mt-2 text-blue-100">
          Get unlimited access to all features and support development.
        </p>
        <Link
          href="/app/pricing" // This is the "how to buy" link
          className="mt-6 inline-flex items-center justify-center gap-2 px-8 py-3 bg-white text-blue-700 font-semibold rounded-lg shadow-md hover:bg-gray-100 transition-all duration-200 transform hover:-translate-y-0.5"
        >
          <ArrowUpCircleIcon className="h-5 w-5" />
          View Pro Plans
        </Link>
      </div>
    </div>
  );
}

// This is the component for Paid users
function ProHub() {
  const quickLinks = [
    {
      name: "My Tasks",
      href: "/app/tasks",
      icon: ListBulletIcon,
      description: "View all your to-dos",
    },
    {
      name: "My Notes",
      href: "/app/notes",
      icon: PencilIcon,
      description: "Capture a new idea",
    },
    {
      name: "My Diary",
      href: "/app/diary",
      icon: BookOpenIcon,
      description: "Write today's entry",
    },
    {
      name: "Focus Timer",
      href: "/app/pomodoro",
      icon: ClockIcon,
      description: "Start a focus session",
    },
  ];

  return (
    <div className="space-y-8">
      {/* Tier Status Card */}
      <div className="rounded-lg border border-green-200 bg-gradient-to-r from-green-50 to-emerald-50 p-6 shadow-sm">
        <h3 className="text-xl font-semibold text-green-900">
          Welcome to TaskGlyph Pro!
        </h3>
        <p className="mt-1 text-green-700">
          You have unlimited access to all features. Thank you for your support!
        </p>
      </div>

      {/* Quick Links Grid (Not redundant for paid users) */}
      <div>
        <h2 className="text-xl font-semibold text-gray-900 mb-4">
          Quick Access
        </h2>
        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
          {quickLinks.map((link) => (
            <Link
              key={link.name}
              href={link.href}
              className="group relative flex flex-col p-6 bg-white rounded-lg shadow-md border border-gray-200 hover:shadow-lg hover:border-blue-500 transition-all duration-300 transform hover:-translate-y-1"
            >
              <span className="p-3 bg-gray-100 text-gray-700 rounded-lg inline-flex group-hover:bg-blue-100 group-hover:text-blue-600 transition-colors">
                <link.icon className="h-6 w-6" />
              </span>
              <h3 className="mt-4 text-lg font-semibold text-gray-900">
                {link.name}
              </h3>
              <p className="mt-1 text-sm text-gray-500">{link.description}</p>
            </Link>
          ))}
        </div>
      </div>
    </div>
  );
}

// Main Page Component
export default function AppHomePage() {
  const { data: session } = useSession();
  const tier = useTier();
  const isFree = tier === "free";

  // Show loading skeleton while tier is being determined
  if (!tier) {
    return (
      <div className="space-y-8 animate-pulse">
        <div className="h-10 w-2/3 bg-gray-200 rounded"></div>
        <div className="h-24 w-full bg-gray-200 rounded-lg"></div>
        <div className="h-8 w-1/3 bg-gray-200 rounded"></div>
        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
          <div className="h-36 bg-gray-200 rounded-lg"></div>
          <div className="h-36 bg-gray-200 rounded-lg"></div>
          <div className="h-36 bg-gray-200 rounded-lg"></div>
          <div className="h-36 bg-gray-200 rounded-lg"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-10">
      {/* Welcome Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">
          Welcome back, {session?.user?.name?.split(" ")[0] || "User"}!
        </h1>
      </div>

      {/* Render different content based on tier */}
      {isFree ? <UpgradeSection /> : <ProHub />}
    </div>
  );
}
